import Vuex from 'vuex'
import Vue from 'vue'
import actions from './actions'
import mutations from './mutations'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    topics:[
      { name: 'all', desc: '' },
      { name: 'good', desc: '' },
      { name: 'share', desc: '' },
      { name: 'ask', desc: '' },
      { name: 'job', desc: '' },
      { name: 'dev', desc: '' }
    ],
    moreMenus_unLogin: {
      login: ''
    },
    moreMenus_logined: {
      create: '',
      center: '',
      notification: '',
      logout: ''
    },
    isShowMore: false,
    isLogined: false,
    userinfo: {
      loginname: '',
      avatar_url: '',
      id: ''
    }
  },
  getters: {
    moreMenus(state) {
      return state.isLogined?state.moreMenus_logined:state.moreMenus_unLogin;
    }
  },
  actions: actions,
  mutations: mutations

})



